<template>
  <div>
    <z-paging
      ref="pagingRef"
      v-model="dataList"
      @query="queryList"
      :loading-more-custom-style="{ 'padding-bottom': '200rpx' }"
    >
      <template #top>
        <view class="top-box">
          <uv-tabs
            :list="showList"
            :scrollable="false"
            @click="handleStatus"
            :lineColor="activitedColor"
          ></uv-tabs>
        </view>
        <trainDropDown @search="search" ref="homeDropDownRef" />
      </template>
      <view class="main-box">
        <TrainItem v-for="(item, index) in dataList" :key="index" :item="item" />
        <view v-if="dataList.length == 0" style="min-height: 100vh">
          <empty></empty>
        </view>
      </view>
      <template #empty></template>
    </z-paging>
  </div>
</template>

<script>
import TrainItem from '@/components/TrainItem/index.vue'
import trainDropDown from './components/trainDropDown.vue'
import shareMixins from '@/mixins/shareMixins'
export default {
  mixins: [shareMixins],
  components: {
    TrainItem,
    trainDropDown,
  },
  data() {
    return {
      area: '',
      urls: [],
      pickTab: 0,
      dataList: [],
      areaGroupList: [
        [
          {
            text: '全部区域',
            value: 0,
          },
        ],
      ],
      tempDataObj: {},
      searchObj: {},
    }
  },
  computed: {
    activitedColor() {
      return this.$store.app.themeConfig['hubei-primary']
    },
    themeType() {
      return this.$store.app.themeType
    },
    showList() {
      if (this.themeType == '1') {
        return [
          {
            value: 0,
            name: '全部活动',
          },
          {
            value: 1,
            name: '晋级培训',
          },
          {
            value: 2,
            name: '技能培训',
          },
        ]
      } else {
        return [
          {
            value: 0,
            name: '全部活动',
          },
          {
            value: 1,
            name: '晋级培训',
          },
          {
            value: 2,
            name: '技能培训',
          },
          {
            value: 3,
            name: '校园培训',
          },
        ]
      }
    },
  },
  onLoad(options) {},
  onHide() {
    this.$refs.homeDropDownRef?.close()
  },
  methods: {
    resetData() {
      uni.showLoading({
        title: '加载中',
      })
      this.dataList = []
      this.$refs.pagingRef?.complete([])
    },
    handleStatus(item) {
      this.pickTab = item.value
      this.resetData()
      this.queryList(1, 10)
    },
    search(data) {
      this.searchObj = data
      this.resetData()
      this.queryList(1, 10)
    },
    getLabel(data = [], key, keyArray = []) {
      let label = []
      data.forEach((item) => {
        if (item[keyArray[0]] == key) {
          label.push(item[keyArray[1]])
        }
        if (item[keyArray[2]] && item[keyArray[2]].length > 0) {
          label = label.concat(this.getLabel(item[keyArray[2]], key, keyArray))
        }
      })
      return label
    },
    async queryList(pageNo, pageSize) {
      console.log('[ pageNo ] >', pageNo)
      console.log('[ pageSize ] >', pageSize)
      let resAreaGroupList = this.tempDataObj.areaGroupList
      let params = {
        page: pageNo,
        size: pageSize,
        ...this.searchObj,
      }
      if (this.pickTab != 0) {
        params.train_type = this.pickTab
      }
      let res = await this.$api.getWxTrainActivityList(params)
      if (res.code == 200) {
        let dealData = res.data.list.map((item) => {
          let startTime = this.$dayjs(item.apply_start_time * 1000).format('YYYY-MM-DD')
          let endTime = this.$dayjs(item.apply_end_time * 1000).format('YYYY-MM-DD')
          let formatObj = {
            tagStr: item.tag_ids_arr
              .map((tagItem) => {
                return tagItem.tag_id_str
              })
              .toString(),
            startTime: startTime,
            endTime: endTime,
          }
          return {
            ...formatObj,
            ...item,
          }
        })
        // this.dataList = dealData
        this.$refs.pagingRef?.complete(dealData)
        uni.hideLoading()
      }
    },
  },
}
</script>

<style scoped lang="scss">
.sticky {
  top: -1px !important;
  background-color: #fff;
}
.top-box {
  background: #fff;
}
</style>
